/*sprites icons*/
@sprite-url: url(../images/sprite.png);
@icon-arrow-active-position: 0px 0px;
@icon-arrow-active-width: 14px;
@icon-arrow-active-height: 14px;
@icon-arrow-position: -14px 0px;
@icon-arrow-width: 14px;
@icon-arrow-height: 14px;

.icon {
background-image: url(../images/sprite.png);
}
.icon-arrow-active {
    background-position: 0px 0px;
    width: 14px;
    height: 14px;
}
.icon-arrow {
    background-position: -14px 0px;
    width: 14px;
    height: 14px;
}
